"use client";
import React, { useState } from "react";
import { Box, Typography, TextField, Slider } from "@mui/material";

export default function LoanAmount() {
  const [loanAmount, setLoanAmount] = useState(100000);
  const [interestRate, setInterestRate] = useState(10);
  const [loanDuration, setLoanDuration] = useState(10);

  const handleLoanAmountChange = (e) => {
    setLoanAmount(e.target.value);
  };

  const handleInterestRateChange = (e, value) => {
    setInterestRate(value);
  };

  const handleLoanDurationChange = (e, value) => {
    setLoanDuration(value);
  };

  return (
    <Box
      sx={{
        width: "682.5px",
        height: "446px",
        borderRadius: "8px",
        backgroundColor: "#fff",
        padding: "32px",
        boxShadow: "0px 4px 10px rgba(0, 0, 0, 0.1)",
        boxSizing: "border-box",
      }}
    >
      <Typography
        sx={{
          fontWeight: 600,
          fontSize: "16px",
          color: "#484848",
          marginBottom: "16px",
        }}
      >
        What Is The Amount Of Loan You Want To Borrow?
      </Typography>
      <TextField
        type="number"
        value={loanAmount}
        onChange={handleLoanAmountChange}
        sx={{
          width: "100%",
          marginBottom: "24px",
          "& .MuiInputBase-root": {
            fontSize: "16px",
            padding: "10px 12px",
          },
        }}
        InputProps={{
          startAdornment: (
            <Typography
              sx={{
                fontWeight: 600,
                fontSize: "16px",
                color: "#484848",
                marginRight: "8px",
              }}
            >
              â‚¹
            </Typography>
          ),
        }}
      />
      <Typography
        sx={{
          fontWeight: 500,
          fontSize: "16px",
          color: "#484848",
          marginBottom: "8px",
        }}
      >
        Rate Of Interest (P.A)
      </Typography>
      <Slider
        value={interestRate}
        onChange={handleInterestRateChange}
        min={0}
        max={20}
        step={0.1}
        valueLabelDisplay="auto"
        sx={{
          marginBottom: "24px",
          color: "#1976d2",
          "& .MuiSlider-thumb": {
            width: "14px",
            height: "14px",
          },
          "& .MuiSlider-track": {
            height: "6px",
          },
          "& .MuiSlider-rail": {
            height: "6px",
          },
        }}
      />
      <Typography
        sx={{
          fontWeight: 500,
          fontSize: "16px",
          color: "#484848",
          marginBottom: "8px",
        }}
      >
        Duration Of The Loan
      </Typography>
      <Slider
        value={loanDuration}
        onChange={handleLoanDurationChange}
        min={1}
        max={30}
        step={1}
        valueLabelDisplay="auto"
        sx={{
          color: "#1976d2",
          "& .MuiSlider-thumb": {
            width: "14px",
            height: "14px",
          },
          "& .MuiSlider-track": {
            height: "6px",
          },
          "& .MuiSlider-rail": {
            height: "6px",
          },
        }}
      />
    </Box>
  );
}
